
.form-control::placeholder, p, .form-control{
    color: #134383;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}
.form-control::placeholder{
  font-size: 14px;
}

body{
  background-color: #f0f0f0;
  height: 100vh;

}
.centeralize{
  margin-top:0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 375px;
}

#root{
  position: relative;
  height: 100vh;
  margin-bottom: 50px;
}

/* header & footer */
.arved-navbar, .arved-footer{
  background-color: #134383;
  position:fixed;
  left: 0px;
  width: 100%;
  display: flex;
}

.arved-footer{
  justify-content: center;
  align-items: center;
  bottom: 0px;
  height: 5vh;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: white;
}

.arved-navbar{
  background-color: #134383;
  top: 0px;
  left:0;
  height: 60px;
  z-index: 1000
}

.arved-text{
  font-family: 'Cinzel', sans-serif;
  font-size: 104px;
  width: fit-content;
}

/* labels and inputs*/
.arved-input-label, .arved-input-label2{
  background-color: #f0f0f0;
  border: none;
  border-radius: 0%;
  height:  30px;
}
.arved-input-label2{
  background-color: #ffffff;
}

.arved-input-label:focus{
  box-shadow: none;
  outline: 0;
  background-color: transparent;
}
.arved-input-group{
  border-bottom: 2px solid #134383;
  color:#134383;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  margin-bottom: 30px;
}

.button-group{
  width: 30%;
}
.arved-button1{
  background-color: #f0f0f0;
  color: #134383;
  border-radius: 4px;
  border: 2px solid #134383;
  font-size: 12px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}
.arved-button1:hover{
  background-color: #134383;
  border-color:#134483b4;
  color: #f0f0f0;
}
.arved-button2{
  background-color: #ffffff;
  color: #134383;
  font-size: 18px;
  border: 0px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  outline: none;
}
.arved-button2:hover{
  background-color: #ffffff;
  color: #134383;
  text-decoration: underline;
}
.arved-button2:focus{
  outline: none;
}

.arved-button3, .arved-button4{
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  border: 0px;
  border-radius: 2px;
  height: 48px;
  width: 100px;
}
.arved-button3{
  background-color: #134383;
  color: #ffffff;
}
.arved-button4{
  background-color: #f0f0f0;
  color: #134383;
}
.arved-button3:hover{
  background-color: #28a745;
  color: #ffffff;
}
.arved-button4:hover{
  background-color: #dc3545;
  color: #ffffff;
}
/*texts*/
.new-account-text{
  font-size: 19px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: #495A70;
}
.forgot-password-text{
  font-size: 15px;
  font-weight:400;
  font-family: Arial, Helvetica, sans-serif;
  color: #949494
}
.forgot-password-text a{
  color: #949494;
  text-decoration: underline;
}
.forgot-password-text a:hover{
  color: #134383;
  text-decoration: underline;
}

.nav-link{
  color: rgba(255,255,255,1);

}

.navbar{
  padding: 0
}

.option{
  margin: 0px 8px;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  display: flex;
  align-items:center;
  justify-content: space-between;
  gap: 2px;
}

.content{
  margin: 90px 0px;  
}
.product-page{
  width: 70%;
  margin: 90px auto;  

}
.product-form{
  background-color: #ffffff;
  border: 1px solid #707070;
}
.product-text{
  margin-left: 20px;
  margin-bottom: 5px;
  color: #134383;
  font-size: 33px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}
.profile{
  width: 100%;
  height: 180px;
  margin-bottom: 20px;
  border: 1px solid #707070;
  border-radius: 10px;
  background-color: #ffffff;
  overflow:hidden;
  margin-left: 50px;
}

.department, .field, .title, .table th, .table td, .product-title, .product-author, .product-magazine{
  color: #134383;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}
.title{
  font-size: 30px;
  margin-bottom: 0px;
}
.department{
  font-size: 20px;
  margin: 7px 0px 15px;
}
.field{
  font-size: 16px;
  padding-right: 0px;
  padding-left: 15px;
  margin-left: 15px;
  /* width: 45%; */
  min-width: 35%;
  position: relative;
}

.field::before {
  content: 'â€¢';
  position: absolute;
  left: 5px;
}

.product-bar{
  width: 100%;
  height: 53px;
  margin-bottom: 20px;
  border: 1px solid #707070;
  border-radius: 10px;
  background-color: #ffffff;
  overflow: hidden;
}
.products{
  width: 100%;
}
.product{
  width: 100%;
  height: fit-content;
  margin-bottom: 20px;
  border: 1px solid #707070;
  border-radius: 10px;
  background-color: #ffffff;
  overflow: hidden;
}
.product-section{
  width: 72%;
}
.product-type{
  /* width: ; */
  text-align: center;
  /* margin-right: 50px; */
}
.product-year{
  width: auto;
  text-align: center;
  margin-right: 50px;
}
.product-type-year{
  width: 180px;
  margin-right: 0px;
}
.product-title{
  margin: 0px;
  line-height: 1.3em;
}
.product-author{
  margin: 0px;
  font-size: 16px;
  color: #85909E;
}
.product-magazine{
  margin: 0px;
  font-size: 16px;
  color: #85909E;
}
.table th, .product-title{
  font-size: 20px;
  border-width: 0px;
}
.table td{
  font-size: 20px;
  border: transparent;
}
.userInfo{
  margin-left: 10px;
}

textarea[name="description"]
{
  margin-top: 15px;
  border: 2px solid #134383;
}

input[type="date"]::-webkit-calendar-picker-indicator{
  display: none;
}
.select-group{
  width: calc(100% - 32px);
  border: 0;
  outline: 0;
  color: #134383;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin-left: 7px;
  text-transform: capitalize;
  height: 30px;
}
input[type="file"]::-webkit-file-upload-button{
  display: none;
}

.arved-input-label2:focus{
  box-shadow: none;
  outline: 0;
  color:#28a745;
  background-color: transparent;
}

.no-style{
  padding: 0;
  background-color: transparent;
  border: 0;
  color: transparent;
}

.no-style:focus{
  outline: 0;
  border: 0;
}